@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Book.ttf);
}

* {
  box-sizing: border-box;
}
  
html {
  font-family: "Avenir", sans-serif;
  font-weight: 300;
}
  
.search-bar {
  position: relative;
  width: 100%;
  margin: 20px 0;
}
  
.img-big {
  position: absolute;
  left: 26px;
  top: 25px;
  width: 19px;
  height: 19px;
}
  
.img-small {
  position: absolute;
  left: 13px;
  top: 15px;
  width: 11px;
  height: 11px;
}
  
.search {
  width: 100%;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  background: #fff;
  color: #3d4e61;
}
  
.big {
  font-family: inherit;
  padding-left: 62px;
  height: 70px;
  font-size: 16px;
  line-height: 22px;
}
  
.small {
  font-family: inherit;
  padding-left: 33px;
  height: 42px;
  font-size: 14px;
  line-height: 19px;
}

.search:hover {
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search:focus {
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  font-weight: 900;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}
